<template>
  <component :is="tag" class="content" v-html="parsedMarkdown" />
</template>

<script>
import marked from "marked";
export default {
  name: "Content",
  props: {
    input: {
      type: String,
      required: true,
      default: ""
    },
    tag: {
      type: String,
      required: false,
      default: "div"
    }
  },
  computed: {
    parsedMarkdown() {
      return marked(this.input);
    }
  }
};
</script>

<style scoped lang="scss"></style>
